body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  /* background-color: ; */
  /* font-family: ; */
}

a {
  text-decoration: inherit;
  /* text-decoration: none; */
  color: #6F90F4;
}
a:hover {
  color: #82D1F1;
}

li {
  list-style: none;
}

.btnA {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  /* display: inline-block; */
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #ACE7F8;
  border: 2px solid #ACE7F8;
  text-decoration: none;
}

.btnA:hover {
  background: #ACE7F8;
  color: black !important;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
}
.spinner-border {
  height: 100px;
  width: 100px;
}

